body {
  margin: 0;

}



html, body {
  max-width: 100%;
  height: 100%;
}

.container-fluid{
  margin:0 !important;
  padding: 0 !important;
}


#custom-navbar{
	padding:0 !important;
}

.navbar-collapse{
justify-content: flex-end!important;
margin-right : 8%;
}


.fadein-navbar{
  position: fixed !important;
  top: 0;
  right: 0;
  left: 0;
	z-index: 10;
	height: 60px;
  background-color: transparent !important;
	text-transform: uppercase;
  font-size: 0.8em;
	font-family: 'Poppins', sans-serif !important;
	transition: all 0.4s ease-in-out;
}

.fadeout-navbar{
	background-color: whitesmoke !important;
	position: fixed !important;
	height: 60px;
  top: 0;
  right: 0;
  left: 0;
	z-index: 10;
  text-transform: uppercase;
	font-size: 0.8em;
	font-family: 'Poppins', sans-serif !important;
	transition: all 0.4s ease-in-out;
	box-shadow: 0px 1px 10px rgb(151, 150, 150);
}



.text-menu-b{
	color:black;
}

.text-menu-w{
	color:whitesmoke;
}

.resumeLogo{
	font-family: 'Rubik', sans-serif !important;
	font-weight: 1000;
	font-size: 2.5em;
	color:#e7cd24 !important;
}

a:hover {
	color: #e7cd24 !important;
}


.toggler-custom{
  border:none !important;
}

.fa-bars {
  color: #e7cd24 !important;
  font-size: 2em;
}

.navbar-toggler{
	border: none !important;
  outline : none !important;
}


@media screen and (max-width : 768px) {
	.resumeLogo{
		margin-left:10px !important;
		padding-bottom: 0;
		font-size : 2em;
	}
	#custom-navbar{
		background-color: whitesmoke !important;
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	}
	.navbar-toggler{
	font-size: 1.2em;
	}
	.fadein-navbar{
		height: 45px;
	}

	.navbar-collapse {
		background-color: whitesmoke !important;
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
		margin:0 !important;
		font-family: 'Rubik', sans-serif !important;
		text-transform: initial;
		text-align: center !important;
		justify-content: center !important;
	}
	.text-menu-w{
		font-size: 1.3em;
		color: rgb(71, 70, 70) !important;
	}
}





.media-container {
  max-height: 100vh !important;
  overflow-y: hidden; 
  position: relative;
  text-align: center;

}

.media-home {
  width: 100vw;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  object-fit: cover;
}
  
.my-text{
  position: absolute;
  top: 30%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: whitesmoke;
  font-family: 'Rubik', sans-serif !important;
	font-weight: 1000;
	font-size: 2.5em;
  }


  @media screen and (max-width : 768px) {
    .my-text{
      position: absolute;
      top: 36%;
      left: 35%;
    }
  }


.about-global-wrap-custom {
  padding-bottom: 40px !important;
  padding-top: 40px !important;
}

.about-global-wrap-custom > h3 {
  margin-top: 0 !important;
}
.about-wrap-custom {
  
  font-family: 'Poppins', sans-serif !important;
}

.picture-col {
  margin-right: 0 !important;
}

.social-col {
  font-size: 0.9em;
  font-weight: lighter !important;
  color: rgb(24, 23, 23) !important;
}

.social-col > a:hover {
  background-color:#e7cd24 !important;
  color:rgb(56, 55, 55) !important;
}

.social-col > a {
  font-weight: bold;
  background-color: black;
  border: none;
  width: 40px;
}

.phone-button {
  font-weight: bold;
  background-color: black;
  border: none;
  width: 40px;
  height: 35px;
  font-size: 25px;
}

.phone-button:hover {
  background-color:#e7cd24 !important;
  color:black !important;
  border: none;
}

.email-button > p {
  margin-bottom: 0 !important;
  margin-top: 3px !important;
}

.email-col {
  font-size: 0.9em !important;
}

.email-button {
  font-weight: bold;
  background-color: black;
  border: none;
}

.email-button:hover {
  background-color: black;
  border: none;
  cursor: default !important;
}

.media-about {
  width: 100%;
  min-height: 100%;
  position: relative;
  overflow: hidden;
  object-fit: cover;
  border-radius: 3px !important;
}

.profile-col {
font-size: 0.9em;
}

@media screen and (max-width : 768px) {
.media-about {
  display: none;
  }
  .social-col {
    text-align: center;
    margin-bottom: 40px;
  }
  .phone-col {
    margin-top: 5px;
  }
  .phone-custom {
    display: block;
    margin-bottom: 20px;
  }
  .email-custom {
    font-size: 1em;
    display: block;
  }
  .profile-col {
    text-align: center;
  }
}

@media screen and (max-width : 992px) {
    .social-tidy {
      text-align: center;
      margin-bottom: 40px;
    }
    .profile-col {
      text-align: center;
    }
    .phone-custom {
      display: block;
      margin-bottom: 20px;
    }
    .email-custom {
      font-size: 1em;
      display: block;
    }
  }

  @media screen and (min-width: 992px) and (max-width: 1220px) {
  .email-col {
    font-size: 0.79em !important;
  }
  .phone-col {
    font-size: 0.9em !important;
  }
}
body {
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

h1 {
  font-size: 3rem;
  letter-spacing: 1px;
  margin: 0;
  padding: 0;
  text-align: center;
  opacity: 0.5;
}

.wrap-custom-skills {
  height: 600px;
  position: relative;
  background-color: whitesmoke;
  padding-top: 30px !important;
}

.app-outer {
  align-items: center; 
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  left: 0;
  padding: 0;
  position: absolute;
  right: 0;
  top: 0;
}

.app-inner {
  display: flex;
  flex-direction: column;
  height: 100%;
  max-width: 800px;
  width: 100%;
}

.tag-cloud {
  flex: 1 1;
}

.tag-cloud > div {
  transition: 2s;
}

@media screen and (min-width: 768px) {
  .app-inner {
    display: flex;
    flex-direction: column;
    height: 100%;
    max-width: 1000px;
    width: 100%;
    margin-left:100px !important;
    margin-top: 50px !important;
  }
  .wrap-custom-skills{
    height: 450px;
    position: relative;
    margin-top: 40px !important;
    padding-top: 30px !important;
  }
  .javascript-skill{
    display: none;
  }
}

@media screen and (max-width: 768px) {
  .app-outer {
    margin-top: 100px;
    padding-top: 10px;
    height: 700px;
  }
  .app-inner {
    display: flex;
    flex-direction: column;
    height: 150%;
    max-width: 768px;
    width: 100%;
  }
  .wrap-custom-skills{
    height: 800px;
    position: relative;
    margin-top: 20px !important;
  }
}








.container-xp-custom {
  background-color: #e7cd24 !important;;
  overflow-y: hidden; 
}

